.characterImg {
	width: 100%;
	height: 300px;
	object-fit: cover;
	border-radius: 10px;
	position: relative;
	transition: 0.8s;
}

.characterData {
	position: absolute;
	height: 300px;
	width: 257px;
	background-color: rgb(78, 126, 23);
	border-radius: 10px;
	padding: 20px;
}

.characterName {
	font-size: 24px;
}

.characterImg:hover {
	opacity: 0;
	transform: translateY(30px);
}

.chatacters {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-column-gap: 10px;
}

.characterInfoOpacity {
	opacity: 0;
	transition: 1s;
}
